export default class NodeInfo{
  constructor(node, path, parent, root) {
  	this.node = node;
  	this.path = path || [];
  	this.parent = parent || [];
  	this.root = root || node;
  }
  concat(node, path) {
    if (!this._parent) {
      this._parent = this.parent.slice();
      this._parent.push(this.node);
    }

    return new NodeInfo(
      node, 
      this.path.concat(path),
      this._parent,
      this.root
    );
  }
}


